// TPV Studio - Layers Panel Component
// Displays all courts and tracks in a draggable list for layer ordering
import React, { useState, useEffect } from 'react';
import { useSportsDesignStore } from '../../stores/sportsDesignStore.js';

/**
 * LayersPanel Component
 * Shows all elements (courts and tracks) in layer order with drag-to-reorder
 *
 * Elements are displayed top-to-bottom (top layer first visually)
 * but stored bottom-to-top in elementOrder array
 */
function LayersPanel() {
  const {
    courts,
    tracks,
    motifs,
    shapes,
    texts,
    exclusionZones,
    groups,
    elementOrder,
    selectedCourtId,
    selectedTrackId,
    selectedMotifId,
    selectedShapeId,
    selectedTextId,
    selectedExclusionZoneId,
    selectedGroupId,
    editingGroupId,
    setElementOrder,
    selectCourt,
    selectTrack,
    selectMotif,
    selectShape,
    selectText,
    selectExclusionZone,
    selectGroup,
    deselectGroup,
    enterGroup,
    exitGroup,
    ungroup,
    deleteGroupWithChildren,
    bringToFront,
    sendToBack,
    duplicateCourt,
    duplicateTrack,
    duplicateMotif,
    duplicateShape,
    duplicateText,
    removeCourt,
    removeTrack,
    removeMotif,
    removeShape,
    removeText,
    removeExclusionZone,
    renameElement,
    toggleElementLock,
    toggleElementVisibility,
    addToHistory
  } = useSportsDesignStore();

  // Drag state
  const [draggedId, setDraggedId] = useState(null);
  const [dragOverId, setDragOverId] = useState(null);

  // Overflow menu state
  const [menuOpenId, setMenuOpenId] = useState(null);

  // Rename state
  const [renamingId, setRenamingId] = useState(null);
  const [renameValue, setRenameValue] = useState('');

  // Expanded groups state
  const [expandedGroups, setExpandedGroups] = useState(new Set());

  // Close menu when clicking outside
  useEffect(() => {
    const handleClickOutside = () => setMenuOpenId(null);
    if (menuOpenId) {
      document.addEventListener('click', handleClickOutside);
      return () => document.removeEventListener('click', handleClickOutside);
    }
  }, [menuOpenId]);

  // Display order is reversed (top layer appears first in UI)
  const displayOrder = [...elementOrder].reverse();

  // Get element details
  const getElementInfo = (elementId) => {
    if (elementId.startsWith('court-')) {
      const court = courts[elementId];
      return {
        type: 'court',
        name: court?.customName || court?.template?.name || 'Unknown Court',
        icon: '',
        sport: court?.template?.sport || '',
        locked: court?.locked || false,
        visible: court?.visible !== false // Default to true
      };
    } else if (elementId.startsWith('track-')) {
      const track = tracks[elementId];
      return {
        type: 'track',
        name: track?.customName || track?.template?.name || 'Unknown Track',
        icon: '',
        sport: 'athletics',
        locked: track?.locked || false,
        visible: track?.visible !== false
      };
    } else if (elementId.startsWith('motif-')) {
      const motif = motifs[elementId];
      return {
        type: 'motif',
        name: motif?.customName || motif?.sourceDesignName || 'Unknown Motif',
        icon: '',
        sport: 'playground',
        locked: motif?.locked || false,
        visible: motif?.visible !== false
      };
    } else if (elementId.startsWith('shape-')) {
      const shape = shapes[elementId];

      // Determine name and icon based on shape type
      let shapeName = 'Shape';
      let icon = '';

      if (shape?.shapeType === 'blob') {
        // Blob shapes - use style name
        const styleNames = {
          organic: 'Organic Blob',
          splashy: 'Splashy Blob',
          cloudy: 'Cloudy Blob',
          rocky: 'Rocky Blob'
        };
        shapeName = styleNames[shape.blobStyle] || 'Blob';
        icon = '';
      } else if (shape?.shapeType === 'path') {
        // Path shapes
        shapeName = shape.closed ? 'Custom Shape' : 'Path';
        icon = '';
      } else if (shape?.starMode) {
        // Star shapes
        shapeName = `${shape.sides || 5}-Point Star`;
        icon = '';
      } else if (shape?.sides) {
        // Regular polygons
        const sides = shape.sides;
        if (sides >= 32) {
          shapeName = 'Circle';
          icon = '';
        } else if (sides === 3) {
          shapeName = 'Triangle';
          icon = '';
        } else if (sides === 4) {
          shapeName = 'Rectangle';
          icon = '';
        } else if (sides === 5) {
          shapeName = 'Pentagon';
          icon = '';
        } else if (sides === 6) {
          shapeName = 'Hexagon';
          icon = '';
        } else if (sides === 8) {
          shapeName = 'Octagon';
          icon = '';
        } else {
          shapeName = `${sides}-gon`;
          icon = '';
        }
      }

      return {
        type: 'shape',
        name: shape?.customName || shapeName,
        icon: icon,
        sport: 'shape',
        locked: shape?.locked || false,
        visible: shape?.visible !== false
      };
    } else if (elementId.startsWith('text-')) {
      const text = texts[elementId];
      const displayContent = text?.content || 'Text Label';
      const truncatedName = displayContent.length > 20 ? displayContent.substring(0, 20) + '...' : displayContent;
      return {
        type: 'text',
        name: text?.customName || truncatedName,
        icon: 'T',
        sport: 'text',
        locked: text?.locked || false,
        visible: text?.visible !== false
      };
    } else if (elementId.startsWith('exclusion-')) {
      const zone = exclusionZones[elementId];
      return {
        type: 'exclusion',
        name: zone?.customName || 'Exclusion Zone',
        icon: '',
        sport: 'exclusion',
        locked: zone?.locked || false,
        visible: zone?.visible !== false
      };
    }
    return { type: 'unknown', name: 'Unknown', icon: '', sport: '', locked: false, visible: true };
  };

  // Check if element is selected
  const isSelected = (elementId) => {
    return elementId === selectedCourtId || elementId === selectedTrackId || elementId === selectedMotifId || elementId === selectedShapeId || elementId === selectedTextId || elementId === selectedExclusionZoneId;
  };

  // Handle element selection
  const handleSelect = (elementId) => {
    if (elementId.startsWith('court-')) {
      selectCourt(elementId);
    } else if (elementId.startsWith('track-')) {
      selectTrack(elementId);
    } else if (elementId.startsWith('motif-')) {
      selectMotif(elementId);
    } else if (elementId.startsWith('shape-')) {
      selectShape(elementId);
    } else if (elementId.startsWith('text-')) {
      selectText(elementId);
    } else if (elementId.startsWith('exclusion-')) {
      selectExclusionZone(elementId);
    }
  };

  // Drag handlers
  const handleDragStart = (e, elementId) => {
    setDraggedId(elementId);
    e.dataTransfer.effectAllowed = 'move';
    e.dataTransfer.setData('text/plain', elementId);

    // Add dragging class after a tick
    setTimeout(() => {
      e.target.classList.add('layer-item--dragging');
    }, 0);
  };

  const handleDragEnd = (e) => {
    e.target.classList.remove('layer-item--dragging');
    setDraggedId(null);
    setDragOverId(null);
  };

  const handleDragOver = (e, elementId) => {
    e.preventDefault();
    e.dataTransfer.dropEffect = 'move';

    if (elementId !== draggedId) {
      setDragOverId(elementId);
    }
  };

  const handleDragLeave = () => {
    setDragOverId(null);
  };

  const handleDrop = (e, targetId) => {
    e.preventDefault();

    if (!draggedId || draggedId === targetId) {
      setDraggedId(null);
      setDragOverId(null);
      return;
    }

    // Reorder: move dragged item to target position
    // Remember: displayOrder is reversed from elementOrder
    const newElementOrder = [...elementOrder];

    // Find indices in the actual elementOrder array
    const draggedIndex = newElementOrder.indexOf(draggedId);
    const targetIndex = newElementOrder.indexOf(targetId);

    if (draggedIndex === -1 || targetIndex === -1) return;

    // Remove dragged item
    newElementOrder.splice(draggedIndex, 1);

    // Insert at new position
    // Since display is reversed, we need to adjust:
    // - Dropping "above" in UI = higher index in elementOrder (toward end)
    // - Dropping "below" in UI = lower index in elementOrder (toward start)
    const displayDraggedIndex = displayOrder.indexOf(draggedId);
    const displayTargetIndex = displayOrder.indexOf(targetId);

    if (displayDraggedIndex < displayTargetIndex) {
      // Dragged down in display = move toward start of elementOrder
      newElementOrder.splice(targetIndex, 0, draggedId);
    } else {
      // Dragged up in display = move toward end of elementOrder
      newElementOrder.splice(targetIndex, 0, draggedId);
    }

    setElementOrder(newElementOrder);
    addToHistory();

    setDraggedId(null);
    setDragOverId(null);
  };

  // Quick actions
  const handleBringToFront = (e, elementId) => {
    e.stopPropagation();
    bringToFront(elementId);
    setMenuOpenId(null);
  };

  const handleSendToBack = (e, elementId) => {
    e.stopPropagation();
    sendToBack(elementId);
    setMenuOpenId(null);
  };

  const handleDuplicate = (e, elementId) => {
    e.stopPropagation();
    if (elementId.startsWith('court-')) {
      duplicateCourt(elementId);
    } else if (elementId.startsWith('track-')) {
      duplicateTrack(elementId);
    } else if (elementId.startsWith('motif-')) {
      duplicateMotif(elementId);
    } else if (elementId.startsWith('shape-')) {
      duplicateShape(elementId);
    } else if (elementId.startsWith('text-')) {
      duplicateText(elementId);
    }
    setMenuOpenId(null);
  };

  const handleDelete = (e, elementId) => {
    e.stopPropagation();
    const info = getElementInfo(elementId);
    if (confirm(`Delete "${info.name}"?`)) {
      if (elementId.startsWith('court-')) {
        removeCourt(elementId);
      } else if (elementId.startsWith('track-')) {
        removeTrack(elementId);
      } else if (elementId.startsWith('motif-')) {
        removeMotif(elementId);
      } else if (elementId.startsWith('shape-')) {
        removeShape(elementId);
      } else if (elementId.startsWith('text-')) {
        removeText(elementId);
      } else if (elementId.startsWith('exclusion-')) {
        removeExclusionZone(elementId);
      }
    }
    setMenuOpenId(null);
  };

  const handleStartRename = (e, elementId) => {
    e.stopPropagation();
    const info = getElementInfo(elementId);
    setRenamingId(elementId);
    setRenameValue(info.name);
    setMenuOpenId(null);
  };

  const handleRenameSubmit = (elementId) => {
    if (renameValue.trim()) {
      renameElement(elementId, renameValue.trim());
    }
    setRenamingId(null);
    setRenameValue('');
  };

  const handleRenameKeyDown = (e, elementId) => {
    if (e.key === 'Enter') {
      handleRenameSubmit(elementId);
    } else if (e.key === 'Escape') {
      setRenamingId(null);
      setRenameValue('');
    }
  };

  const handleToggleLock = (e, elementId) => {
    e.stopPropagation();
    toggleElementLock(elementId);
    setMenuOpenId(null);
  };

  const handleToggleVisibility = (e, elementId) => {
    e.stopPropagation();
    toggleElementVisibility(elementId);
    setMenuOpenId(null);
  };

  const handleMenuToggle = (e, elementId) => {
    e.stopPropagation();
    setMenuOpenId(menuOpenId === elementId ? null : elementId);
  };

  if (elementOrder.length === 0) {
    return (
      <div className="layers-panel layers-panel--empty">
        <div className="layers-panel__empty-state">
          <span className="layers-panel__empty-icon"></span>
          <p>No elements yet</p>
          <span className="layers-panel__empty-hint">
            Add courts, tracks, motifs, shapes, or text
          </span>
        </div>
      </div>
    );
  }

  return (
    <div className="layers-panel">
      <div className="layers-panel__header">
        <h4>Layers</h4>
        <span className="layers-panel__count">{elementOrder.length}</span>
      </div>

      <div className="layers-panel__hint">
        Drag to reorder layers. Top = front, bottom = back.
      </div>

      <div className="layers-panel__list">
        {displayOrder.map((elementId, displayIndex) => {
          const info = getElementInfo(elementId);
          const selected = isSelected(elementId);
          const isDragOver = elementId === dragOverId;
          const isFirst = displayIndex === 0; // Top layer
          const isLast = displayIndex === displayOrder.length - 1; // Bottom layer
          const isRenaming = renamingId === elementId;

          return (
            <div
              key={elementId}
              className={`layer-item ${selected ? 'layer-item--selected' : ''} ${isDragOver ? 'layer-item--drag-over' : ''} ${info.locked ? 'layer-item--locked' : ''} ${!info.visible ? 'layer-item--hidden' : ''}`}
              draggable={!info.locked && !isRenaming}
              onDragStart={(e) => handleDragStart(e, elementId)}
              onDragEnd={handleDragEnd}
              onDragOver={(e) => handleDragOver(e, elementId)}
              onDragLeave={handleDragLeave}
              onDrop={(e) => handleDrop(e, elementId)}
              onClick={() => !isRenaming && handleSelect(elementId)}
            >
              {/* Drag Handle */}
              <span
                className={`layer-item__handle ${info.locked ? 'layer-item__handle--disabled' : ''}`}
                title={info.locked ? 'Locked' : 'Drag to reorder'}
              >
                {info.locked ? '' : ''}
              </span>

              {/* Element Name or Rename Input */}
              {isRenaming ? (
                <input
                  type="text"
                  className="layer-item__rename-input"
                  value={renameValue}
                  onChange={(e) => setRenameValue(e.target.value)}
                  onBlur={() => handleRenameSubmit(elementId)}
                  onKeyDown={(e) => handleRenameKeyDown(e, elementId)}
                  autoFocus
                  onClick={(e) => e.stopPropagation()}
                />
              ) : (
                <span className={`layer-item__name ${!info.visible ? 'layer-item__name--hidden' : ''}`}>
                  {info.name}
                </span>
              )}

              {/* Status Indicators */}
              <div className="layer-item__indicators">
                {!info.visible && <span className="layer-item__indicator" title="Hidden"></span>}
              </div>

              {/* Quick Actions - Overflow Menu */}
              <div className="layer-item__actions">
                <button
                  className="layer-item__menu-btn"
                  onClick={(e) => handleMenuToggle(e, elementId)}
                  title="More actions"
                >
                  
                </button>

                {menuOpenId === elementId && (
                  <div className="layer-item__menu">
                    <button
                      onClick={(e) => handleBringToFront(e, elementId)}
                      disabled={isFirst}
                    >
                       Bring to Front
                    </button>
                    <button
                      onClick={(e) => handleSendToBack(e, elementId)}
                      disabled={isLast}
                    >
                       Send to Back
                    </button>
                    <div className="layer-item__menu-divider" />
                    <button onClick={(e) => handleDuplicate(e, elementId)}>
                       Duplicate
                    </button>
                    <button onClick={(e) => handleStartRename(e, elementId)}>
                       Rename
                    </button>
                    <div className="layer-item__menu-divider" />
                    <button onClick={(e) => handleToggleLock(e, elementId)}>
                      {info.locked ? ' Unlock' : ' Lock'}
                    </button>
                    <button onClick={(e) => handleToggleVisibility(e, elementId)}>
                      {info.visible ? ' Hide' : ' Show'}
                    </button>
                    <div className="layer-item__menu-divider" />
                    <button
                      onClick={(e) => handleDelete(e, elementId)}
                      className="layer-item__menu-delete"
                    >
                       Delete
                    </button>
                  </div>
                )}
              </div>
            </div>
          );
        })}
      </div>

      {/* Groups Section */}
      {Object.keys(groups).length > 0 && (
        <>
          <div className="layers-panel__section-divider">
            <span>Groups</span>
          </div>
          <div className="layers-panel__list layers-panel__list--groups">
            {Object.entries(groups).map(([groupId, group]) => {
              const isExpanded = expandedGroups.has(groupId);
              const isGroupSelected = selectedGroupId === groupId;
              const isEditing = editingGroupId === groupId;
              const groupName = group.customName ||
                (group.compoundType ? `${group.compoundType.charAt(0).toUpperCase() + group.compoundType.slice(1)} Group` : 'Group');

              return (
                <div key={groupId} className="layer-group">
                  {/* Group header */}
                  <div
                    className={`layer-item layer-item--group ${isGroupSelected ? 'layer-item--selected' : ''} ${isEditing ? 'layer-item--editing' : ''}`}
                    onClick={() => selectGroup(groupId)}
                    onDoubleClick={() => enterGroup(groupId)}
                  >
                    {/* Expand/Collapse toggle */}
                    <button
                      className="layer-item__expand-btn"
                      onClick={(e) => {
                        e.stopPropagation();
                        setExpandedGroups(prev => {
                          const next = new Set(prev);
                          if (next.has(groupId)) {
                            next.delete(groupId);
                          } else {
                            next.add(groupId);
                          }
                          return next;
                        });
                      }}
                      title={isExpanded ? 'Collapse' : 'Expand'}
                    >
                      {isExpanded ? '' : ''}
                    </button>

                    {/* Group icon */}
                    <span className="layer-item__handle layer-item__handle--group">
                      {group.compoundType === 'splash' ? '' :
                       group.compoundType === 'cloud' ? '' :
                       group.compoundType === 'flower' ? '' :
                       group.compoundType === 'abstract' ? '' : ''}
                    </span>

                    {/* Group name */}
                    <span className="layer-item__name">
                      {groupName}
                      <span className="layer-item__child-count">({group.childIds.length})</span>
                    </span>

                    {/* Editing indicator */}
                    {isEditing && (
                      <span className="layer-item__edit-indicator" title="Editing group"></span>
                    )}

                    {/* Group actions */}
                    <div className="layer-item__actions">
                      <button
                        className="layer-item__menu-btn"
                        onClick={(e) => handleMenuToggle(e, groupId)}
                        title="More actions"
                      >
                        
                      </button>

                      {menuOpenId === groupId && (
                        <div className="layer-item__menu">
                          <button onClick={(e) => {
                            e.stopPropagation();
                            enterGroup(groupId);
                            setMenuOpenId(null);
                          }}>
                             Edit Group
                          </button>
                          <button onClick={(e) => {
                            e.stopPropagation();
                            ungroup(groupId);
                            setMenuOpenId(null);
                          }}>
                             Ungroup
                          </button>
                          <div className="layer-item__menu-divider" />
                          <button
                            onClick={(e) => {
                              e.stopPropagation();
                              if (confirm(`Delete group "${groupName}" and all its contents?`)) {
                                deleteGroupWithChildren(groupId);
                              }
                              setMenuOpenId(null);
                            }}
                            className="layer-item__menu-delete"
                          >
                             Delete Group
                          </button>
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Child elements (when expanded) */}
                  {isExpanded && (
                    <div className="layer-group__children">
                      {group.childIds.map(childId => {
                        const childInfo = getElementInfo(childId);
                        const isChildSelected = childId === selectedShapeId || childId === selectedMotifId;

                        return (
                          <div
                            key={childId}
                            className={`layer-item layer-item--child ${isChildSelected ? 'layer-item--selected' : ''}`}
                            onClick={(e) => {
                              e.stopPropagation();
                              // Only allow selecting child if editing the group
                              if (isEditing) {
                                handleSelect(childId);
                              }
                            }}
                          >
                            <span className="layer-item__indent" />
                            <span className="layer-item__handle layer-item__handle--disabled">
                              {childInfo.icon}
                            </span>
                            <span className={`layer-item__name ${!childInfo.visible ? 'layer-item__name--hidden' : ''}`}>
                              {childInfo.name}
                            </span>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              );
            })}
          </div>

          {/* Exit group editing button */}
          {editingGroupId && (
            <div className="layers-panel__exit-group">
              <button
                className="layers-panel__exit-group-btn"
                onClick={() => exitGroup()}
              >
                 Exit Group Editing
              </button>
            </div>
          )}
        </>
      )}

      {/* Exclusion Zones Section (separate from layer order) */}
      {Object.keys(exclusionZones).length > 0 && (
        <>
          <div className="layers-panel__section-divider">
            <span>Exclusion Zones</span>
          </div>
          <div className="layers-panel__list layers-panel__list--exclusions">
            {Object.keys(exclusionZones).map((zoneId) => {
              const info = getElementInfo(zoneId);
              const selected = isSelected(zoneId);

              return (
                <div
                  key={zoneId}
                  className={`layer-item layer-item--exclusion ${selected ? 'layer-item--selected' : ''} ${info.locked ? 'layer-item--locked' : ''} ${!info.visible ? 'layer-item--hidden' : ''}`}
                  onClick={() => handleSelect(zoneId)}
                >
                  <span className="layer-item__handle layer-item__handle--disabled">
                    {info.icon}
                  </span>
                  <span className={`layer-item__name ${!info.visible ? 'layer-item__name--hidden' : ''}`}>
                    {info.name}
                  </span>
                  <div className="layer-item__indicators">
                    {!info.visible && <span className="layer-item__indicator" title="Hidden"></span>}
                  </div>
                  <div className="layer-item__actions">
                    <button
                      className="layer-item__menu-btn"
                      onClick={(e) => handleMenuToggle(e, zoneId)}
                      title="More actions"
                    >
                      
                    </button>

                    {menuOpenId === zoneId && (
                      <div className="layer-item__menu">
                        <button onClick={(e) => handleStartRename(e, zoneId)}>
                           Rename
                        </button>
                        <div className="layer-item__menu-divider" />
                        <button onClick={(e) => handleToggleLock(e, zoneId)}>
                          {info.locked ? ' Unlock' : ' Lock'}
                        </button>
                        <button onClick={(e) => handleToggleVisibility(e, zoneId)}>
                          {info.visible ? ' Hide' : ' Show'}
                        </button>
                        <div className="layer-item__menu-divider" />
                        <button
                          onClick={(e) => handleDelete(e, zoneId)}
                          className="layer-item__menu-delete"
                        >
                           Delete
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              );
            })}
          </div>
        </>
      )}

      <div className="layers-panel__footer">
        <span className="layers-panel__shortcut-hint">
          Tip: Use [ and ] keys to reorder selected element
        </span>
      </div>
    </div>
  );
}

export default LayersPanel;
